.header{
    /* background-color: #131522; */
	overflow-x: hidden;
	margin: 0vh 60px 0 60px;
	/* transition: 0.5s; */
	position: relative;
}
@media (max-width: 768px) {
    .header {
        margin: 0vh 0px 0 0px; /* Adjusted margin for smaller screens */
    }
}
body::-webkit-scrollbar{
    width: 7px;
}
body::-webkit-scrollbar-thumb{
    background-color: #2F3241;
    border-radius: 5px;
}
.main-content{
	width: 100%;
	height: 100%;
	position: relative;
}
.menu-section h1{
	text-align: center;
	font-size: 65px;
	color: #fff;
}
.intro-text{
	text-align: center;
	font-size: 20px;
	color: #fff;
	margin: 38px 150px;
}
.filter-section{
	margin-top: 70px;
	text-align: center;
	background-color: #171b31;
	padding: 30px 0px;
	border-radius: 15px;
	margin-bottom: 30px;
}
.rover-selection, .date-selection , .pov-selection, .find-button{
	display: inline-block;
	margin-right: 15px;
	position: relative;
	transition: 1s all;
}
.rover-selection{
	margin-right: 0;
}
.rover-selection p{
	display: inline-block;
	/* padding: 25px 60px; */
	background-color: #2F3241;
	border-radius: 20px;
	color: #fff;
	/* font-size: 20px; */
	cursor: pointer;
}
.rover-selection p:first-child {
    /* margin-right: 30px;
    width: 10px;
    height: 20px; */
}
.active{
	background-color: #9a00ff !important;
}

.block2 {
    /* padding: 22.5px 60px; */
    background-color: #2F3241;
    border-radius: 20px;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    height: 54px;
    width: 227px;
}
.block2::-webkit-inner-spin-button,::-webkit-calendar-pickher-indicator{
    filter: drop-shadow(16px 16px 20px rgb(255, 255, 255)) invert(100%);
}
.rover-selection input{
	display: none;
}
.pov-selection select {
    padding: 0px 0px 0px 50px;
    background-color: #2F3241;
    border-radius: 20px;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    height: 54px;
    width: 400px;
    padding-left: 27px;
}

.find-button {
    border-radius: 15px;
    background-color: #9a00ff;
    padding: 14px 27px;
    cursor: pointer;
    /* padding: 12px 20px; */
    /* height: 54px; */
    width: 131px;
    padding-top: 21px;
    padding-left: 31px;
    color: white;
    font-size: 20px;
}
.find-button:hover {
    background-color: #5c009a;
}

.rover {
    margin-right: 15px;
}

.find-button label{
	/* padding: 16px; */
	cursor: pointer;
}
