select{
	background-image: url() ; /*aquí deberás escribir la ruta de la imagen que utilizarás como flecha del desplegable*/
	background-repeat: no-repeat;
	background-position: right center;
	-webkit-appearance: none;
	-moz-appearance: none;
	-o-appearance: none;
	appearance: none;
	cursor: pointer;
}
select::-ms-expand {
	display: none; /*Evita que se muestre la flecha por defecto en versiones de IE*/
}
.arrow{
	position: absolute;
	right: 20px;
	top: 20px;
	cursor: pointer;
}
#pov-select-curiosity{
	display: none;
}
.column-element{
	background-color: #2F3241;
	margin: 15px;
	padding: 10px;
	border-radius: 15px;
}

.activeImg{
	position: fixed;
	width: auto;
	height: auto;
	padding: 15px;
	left: 50%;
	top: 50%;
	transform: translate(-50%,-50%);
	z-index: 5;
}

.activeImg > img{
	display: block;
	max-width: 900px;
	max-height: 800px;
	width: 100%;
	height: 100%;
	border-radius: 10px;
	object-fit: contain;
	margin: auto;
}

.popUpImgActive{
	position: fixed;
	left: 0;
	top: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.456);
	z-index: 3;
}

.img-column{
	width: 100%;
	height: 100%;
	border-radius: 15px;
	cursor: pointer;
}
.go-up{
	position: fixed;
	bottom: 30px;
	right: 30px;
	background-color: #9a00ff;
	border-radius: 15px;
	width: 60px;
	height: 60px;
	cursor: pointer;
	opacity: 0;
	transition: 0.5s;
}
.go-up img{
	width: 100%;
	margin-top: -2px;
	transform: rotate(180deg);
}
#search-result-box{
	display: none;
}
.number-of-results p{
	color: #fff;
	font-size: 18px;
	font-weight: 400;
	text-align: center;
	margin: 50px 0px 30px 0px;
}
@media only screen and (max-width: 1440px){
	.pov-selection{
		margin-top: 30px;
	}

	.column-element{
		margin: 15px 0;
	}
}
@media only screen and (max-width: 1024px){
	.intro-text{
		margin: 50px 20px;
	}
	body{
		margin: 15vh 100px 15vh;
	}
	.rover-selection p:first-child {
		margin-right: 15px;
	}
	.rover-selection p:first-child {
    margin-right: 15px;
	}
	.rover-selection p{
		padding: 25px 40px;
	}
	#perseverance{
		margin-left: 25px;
	}
	.pov-selection select{
		margin-left: 30px;
	}
	.column-element{
		margin: 25px 0;
	}
	.go-up{
		bottom: 20px;
    	right: 20px;
	}
}
@media only screen and (max-width: 768px){
	.rover-selection p{
		padding: 25px 60px;
	}
	.block2{
		margin-top: 30px;
		width: 65%;
		margin-left: 30px;
	}
	.date-selection{
		display: block;
		margin-left: 0;
	}
	.pov-selection select{
		margin-left: 30px;
		padding: 25px 35px;
	}
	.find-button{
		width: 50%;
    	margin: 30px 0;
	}
}
@media only screen and (max-width: 425px){
	body{
		margin: 15vh 50px 15vh;
	}
	.menu-section h1{
		font-size: 35px;
	}
	.intro-text{
		margin: 30px 20px;
		font-size: 18px;
	}
	.filter-section{
		margin-top: 50px;
	}
	#curiosity{
		margin-left: 25px;
		margin-top: 15px;
		padding: 25px 85px !important;
	}
	.block2{
		width: auto;
		padding: 22.5px 40px;
	}
	.pov-selection select{
		width: 250px;
	}
}
@media only screen and (max-width: 375px){
	.rover-selection p{
		padding: 25px 40px;
	}
	#curiosity{
		padding: 25px 65px !important;
	}
	.block2{
		padding: 22.5px 20px;
	}
	.pov-selection select{
		width: 220px;
		font-size: 18px;
		padding: 25px 0px 25px 20px;
	}
}
@media only screen and (max-width: 320px){
	body{
		margin: 15vh 20px;
	}
}



